import React from "react"
import { Link } from "gatsby"
import { StaticImage } from "gatsby-plugin-image"
import Layout from "../../components/layout"
import Seo from "../../components/seo"
import "../../styles/main-article-case-study.css"

export default function Home() {
  const description = `神奈川のIT相談ならホットソウルにお任せください！当社がデジタル化やIT活用でサポートさせていただいた実績をご紹介します。 | 2022年8月 有限会社安室設備様 「業務用PCセットアップサポート」`
  const pageUrl = `https://www.hotsoul.jp/case-study/yasumuro-setubi`
  const imageUrl = `https://www.hotsoul.jp/ogp_yasumuro.jpg`

  return (
    <Layout>
      <Seo title="サポート実績" description={description} imageUrl={imageUrl} pageUrl={pageUrl} />
      <div className="wrap">

        <section className="mv_ac-cs">
          <div className="mv_ac-cs-inner">
            <p className="txt1"><span>サポート実績</span></p>
            <p className="txt2">当社がデジタル化や<br className="sp"/>IT活用で<br className="pc"/>サポートさせていただいた<br className="sp"/>実績をご紹介します。
            </p>
          </div>
        </section>

        <section className="ac-cs">
          <h2>業務用PCセットアップサポート</h2>
          <div className="ac-cs-inner">
            <div className="flex">
              <div className="flex-item left">
                <StaticImage
                  src="../../images/main-case-study/yasumuro-setubi/ph1.jpg"
                  alt="有限会社安室設備 様"
                  placeholder="blurred"
                />
              </div>
              <div className="flex-item right">
                <p className="date">2022.11.25</p>
                <p className="title"><span className="bg-yellow">有限会社安室設備 様<br/></span></p>
              </div>
            </div>
            <div className="flex-table-box">
              <div className="flex-table first">
                <div className="flex-table-item left">
                  <p>お悩み</p>
                </div>
                <div className="flex-table-item right">
                  <ul>
                    <li>3台のパソコンを三様にセットアップする必要がある</li>
                    <li>本業が忙しい中で、専門外の慣れない作業に時間が取れない</li>
                    <li>誰かに頼みたいが、誰に頼んでいいかわからない</li>
                  </ul>
                </div>
              </div>
              <div className="flex-table">
                <div className="flex-table-item left">
                  <p>サポート</p>
                </div>
                <div className="flex-table-item right">
                 <ul>
                   <li>事前に社内のIT環境やアカウント管理状況をヒアリング</li>
                   <li>業務の目的に合わせて必要なセットアップ要件をすり合わせ</li>
                   <li>セットアップ作業に必要な情報の事前準備をアドバイス</li>
                   <li>段取りを組み、無駄なく手早い作業実施</li>
                 </ul>
                </div>
              </div>
              <div className="flex-table">
                <div className="flex-table-item left">
                  <p>結果</p>
                </div>
                <div className="flex-table-item right">
                  <ul>
                    <li>ミスやトラブルもなく、安心して作業を任せられた</li>
                    <li>Microsoftアカウントやサブスクについての理解が深まった</li>
                    <li>不安なことや疑問があれば、今後も相談していきたい</li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
          <div className="bg-orange ac-cs-cont">
            <div className="ac-cs-inner">
              <div className="item-box">
                <p className="ttl-support">サポートの背景</p>
                <div className="item first">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">まずは自己紹介と御社のご説明をお願いします。</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">安室さま：</div>
                    <div className="flex-support-item right">有限会社安室設備、代表取締役の安室と申します。弊社の主な業務内容は、上下水道の申請と工事です。市道などの道路下にある水道の本管を新しく交換する工事や、宅地などに水道、下水道を本管につなぐ工事、建物内外の給排水設備などです。そういった工事には官公庁の許可を得るために多種多様の申請図面や書類を作成する必要があります。</div>
                  </div>
                </div>
                <div className="item">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">普段からお仕事やプライベートでパソコン等をお使いですか？</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">安室さま：</div>
                    <div className="flex-support-item right">はい。工事に必要な官公庁の許可を得るための様々な申請図面や書類をパソコンで作成しています。プライベートではもっぱらスマートフォンです。</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">奥さま：</div>
                    <div className="flex-support-item right">私もパソコンでは会計ソフトの入力や、証憑の電子的保存、官公庁の電子申請などをしています。スマートフォンは仕事上でも認証のために必須です。</div>
                  </div>
                </div>
                <div className="item">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">どういう事情でIT活用サポートを受けようと思われましたか？</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">奥さま：</div>
                    <div className="flex-support-item right">仕事で使うパソコンを、3台セットアップする必要がありました。今までパソコンのセットアップは自分たちでやってきたのですが、Microsoftアカウントの登録やパスワードの設定、メールの設定、社内ネットワークの設定など大変な思いをしていました。特に複合機への接続は、うまくいかなくて辟易した経験があります。それに加えて、<span>windows11になってから分からないことも増え</span>、どうしたものかと思案していました。</div>
                  </div>
                </div>
                <div className="item">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">弊社にご相談いただいたきっかけは？</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">奥さま：</div>
                    <div className="flex-support-item right">たまたま商工会議所の会報に同封されたチラシで拝見して「これだ！」と。住所も近かったですし、困っているおじいさんとおばあさんのイラストとオレンジの背景が印象的でした。</div>
                  </div>
                </div>
              </div>
              <StaticImage
                src="../../images/main-case-study/yasumuro-setubi/ph2.jpg"
                alt="サポートの背景"
                placeholder="blurred"
              />
            </div>
          </div>
          <div className="ac-cs-cont">
            <div className="ac-cs-inner">
              <div className="item-box">
                <p className="ttl-support">相談内容とサポート</p>
                <div className="item first">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">ご相談内容は？</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">安室さま：</div>
                    <div className="flex-support-item right">3つのパソコンを三様にセットアップをしてください、とお願いしました。2つは新たに購入したwindows11proの新規セットアップ、1つは既存のwindows10proの再セットアップです。また、それぞれに同時期に導入した複合機のプリンター、スキャナー機能の利用、FAX受信フォルダ設定、Microsoft Officeのインストール、Outlookのメール設定もお願いしました。</div>
                  </div>
                </div>
                <div className="item">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">特にお困りの点は？</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">安室さま：</div>
                    <div className="flex-support-item right">誰に相談すればいいのかわからなかった、ということです。</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">奥さま：</div>
                    <div className="flex-support-item right">パソコンのメーカーに相談したところ、パソコン本体については教えてくれても、周辺機器との接続方法はよくわからない。インターネット回線会社に電話しても、担当者が異動で変わってしまっている。ソフト会社はソフトのことしか教えてくれない。複合機のリース会社も、パソコンの設定はしてくれない。<span>誰かがトータルに見てくれればいいのに</span>、と思っていました。</div>
                  </div>
                </div>
                <div className="item">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">今回、弊社のサポートを受けてみて良かった点をお聞かせください。</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">安室さま：</div>
                    <div className="flex-support-item right">素人の自分たちで行うには時間的にも精神的にも余裕がない時に、<span>安心してお願いできるプロに頼めた</span>ということです。</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">奥さま：</div>
                    <div className="flex-support-item right">お恥ずかしい話ですが、今までパソコンのセットアップは電源を入れて画面の表示に従って、成り行きまかせで行っていました。今回はある程度の準備期間をとって、流れを相談、問題点や疑問点をひとつずつクリアにし、手順を明確化していただきました。その結果、<span>実地の作業時間も短縮</span>されましたし、なにより<span>ミスや心配していたトラブルがありません</span>でした。</div>
                  </div>
                </div>
              </div>
              <StaticImage
                src="../../images/main-case-study/yasumuro-setubi/ph3.jpg"
                alt="相談内容とサポート"
                placeholder="blurred"
              />
            </div>
          </div>
          <div className="bg-orange ac-cs-cont">
            <div className="ac-cs-inner">
              <div className="item-box">
                <p className="ttl-support">今後の展望</p>
                <div className="item first">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">今後も、何かお困りごとがあったら、また相談したいと思いますか？</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">奥さま：</div>
                    <div className="flex-support-item right">トラブルに至らないまでもITの対処は孤軍奮闘だったので、解決が必要な時や今回のような時に、是非ご相談できればと思っています。</div>
                  </div>
                </div>
                <div className="item">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">今回、弊社のサポートを受けてみて良かった点をお聞かせください。</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">奥さま：</div>
                    <div className="flex-support-item right">大きな堅苦しい会社と違って、<span>親しみやすくて頼みやすかった</span>です。</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">安室さま：</div>
                    <div className="flex-support-item right">あとは、やっぱりパソコンや社内ネットワークなど、<span>IT周りのことを包括的に相談できる点</span>ですね。機器などの購入窓口がバラバラで一連のサポートが受けにくく「誰かにやってもらいたいけど、誰にお願いしたらよいかわからない」状態だったので非常に助かりました。</div>
                  </div>
                </div>
                <div className="item">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">逆に、改善点や期待することがあれば。</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">奥さま：</div>
                    <div className="flex-support-item right">初めてお支払いする立場から戸惑った点としては、時間単位の費用は明瞭でしたが、全体としてどのくらいの作業時間を見積ったら良いのか把握が難しかったです。実際には、パソコン、複合機、ネット環境などそれぞれ事前にきっちり調べて把握して頂いた結果、<span>サプライヤーさんなどにお願いするよりも内容は充実していてお安かった</span>と思います。</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">安室さま：</div>
                    <div className="flex-support-item right">期待する点としては、ITに苦手意識を持っている方や、不慣れな地域の方々の助けとなり、ITの力で地域を活性化していただきたいです。少し期待が大きすぎますか？</div>
                  </div>
                </div>
                <div className="item">
                  <div className="flex-support q">
                    <div className="flex-support-item left"></div>
                    <div className="flex-support-item right">安室設備様の今後の展望をお聞かせください。</div>
                  </div>
                  <div className="flex-support a">
                    <div className="flex-support-item left">安室さま：</div>
                    <div className="flex-support-item right">
                      時代の流れに置いて行かれないように必死についていこう！というのが正直なところで、ITに関しても然りです。
                      なるべく「手書き」をなくして、データ化することには早くから取り掛かってはいるのですが、増えた共有データの管理や保存方法に一貫性をもたせることなどは今後の課題です。
                      また、同じ入力や作業を、違うアプリやソフトに反映させるRPA（ロボティック・プロセス・オートメーション）などについても検討中です。
                      原材料や人件費の高騰、働き方改革の一環に対応するためにITの力は不可欠だと思っています。
                      人が集まれば展望も考えられるのですが、なかなか人材が集まらない中なので、ITの力で生産性を上げていくことを当面の目標としています。
                    </div>
                  </div>
                </div>
              </div>
              <StaticImage
                src="../../images/main-case-study/yasumuro-setubi/ph4.jpg"
                alt="今後の展望"
                placeholder="blurred"
              />
			        <div className="flex-share">
                <ul className="sns">
                  <li>
                    <a href="https://twitter.com/share?url=https://www.hotsoul.jp/case-study/yasumuro-setubi" rel="nofollow noopener noreferrer" target="_blank">
                      <StaticImage
                        src="../../images/icon-tw.png"
                        alt="twitterシェア"
                        placeholder="blurred"
                      />
                    </a>
                  </li> 
                  <li>
                    <a href="http://www.facebook.com/share.php?u=https://www.hotsoul.jp/case-study/yasumuro-setubi" rel="nofollow noopener noreferrer" target="_blank">
                      <StaticImage
                        src="../../images/icon-fb.png"
                        alt="facebookシェア"
                        placeholder="blurred"
                      />
                    </a>
                  </li>
                  <li>
                    <a href="https://social-plugins.line.me/lineit/share?url=https://www.hotsoul.jp/case-study/yasumuro-setubi" rel="nofollow noopener noreferrer" target="_blank">
                      <StaticImage
                        src="../../images/icon-line.png"
                        alt="LIMEシェア"
                        placeholder="blurred"
                      />
                    </a>
                  </li>
                </ul>
			        </div>
              <br/>                
              <p className="archive"><Link to={`/case-study/articles`}>他の記事も読む</Link></p>
            </div>
          </div>
        </section>
      </div>
    </Layout>
  )
}